

.to-do-list{
  font-family: Roboto;
  text-align: center;
  margin-top: 100px;
}

input[type="text"] {
  font-family: Roboto;
  font-size: 16px;
  padding: 6px;
  width: 300px;
  border: 2px solid #ddd;
  border-radius: 6px;
  box-shadow: none;
  outline: none;
  transition: border-color 0.3s, box-shadow 0.3s;
  margin-right: 4px;
}
input[type="text"]:focus {
  border-color: #007bff;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.3);
}

button {
  
  padding-top: 9px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 9px;
  border-radius: 5px;
  background-color: #232323;
  border: white;
  
  margin: 3px;
  
  font-family: Roboto;
  font-size: 18px;
  font-weight: 400;
  color: rgb(255, 255, 255);
}
button:hover {
  cursor: pointer;
  background-color: #363636;
}

