/* App.css */

body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Style the navigation bar */
nav {
  width: 160px; /* Skinnier width */
  height: 100vh; /* Full height of the viewport */
  position: fixed; /* Fix it to the top */
  top: 0;
  left: 0;
  background-color: #1b1b1b; /* Background color */
  display: flex;
  align-items: start;
  justify-content: center;
}

/* Style the unordered list inside the navigation */
nav ul {
  list-style-type: none; /* Remove bullets */
  padding: 0;
  margin: 0;
}


nav ul li {
  margin: 20px 0; 
}
nav ul li a {
  color: white;
  text-decoration: none; 
  display: block;
  padding: 10px 10px; 
  border-radius: 5px;
  text-align: center; 
  font-size: 24px; 
  background-color: rgba(64, 64, 64, 0);
  width: 100%;
}


/* Style the links on hover */
nav ul li a:hover {
  background-color: #2f2f2f; /* Change background on hover */
}

/* Add padding to the main content area to avoid overlapping with the fixed nav */
.main-content {
  margin-left: 120px; /* Margin to the left of the fixed nav */
  padding: 20px;
}

.link-routing {
  background-color: rgba(117, 0, 0, 0);
  border: none;
}
